const url = require('./url');
const array = require('./array');
const getNames = require('./format/getNames');
const requestModule = require('../../../requestModule');

module.exports = (function () {

    const Cryptopia = {

        key: 'Cryptopia',
        name: 'Cryptopia',
        getNamesForList: {
            needReq : true,
            req : {
                multi: true,
                fun: requestModule.multipleRequests,
                url : {
                    url1: url.REST_CURRENCY_CRYPTOPIA,
                    url2: url.REST_TICKER_CRYPTOPIA,
                }
            },
            newNames : getNames.getNames
        },
        array: {
            array: array.CRYPTOPIA,
        },
        url: {
            publicURL : url.CRYPTOPIA_URL_TRADE,
            isCapital : true
        }

    };

    return {
        Cryptopia: Cryptopia
    }

})();


