module.exports = (function () {

    const BITTREX_URL = "https://bittrex.com/";
    const BITTREX_URL_TRADE = BITTREX_URL + "Market/Index?MarketName=BTC-{SHORTSYMBOL}";
    const REST_PUBLIC_BITTREX = "https://www.bittrex.com/api/v1.1/public/";
    const REST_CURRENCY_BITTREX = REST_PUBLIC_BITTREX + 'getcurrencies';
    const REST_TICKER_BITTREX = REST_PUBLIC_BITTREX + 'getmarketsummaries';

    return {
        REST_CURRENCY_BITTREX: REST_CURRENCY_BITTREX,
        REST_TICKER_BITTREX: REST_TICKER_BITTREX,
        BITTREX_URL_TRADE: BITTREX_URL_TRADE
    }

})();


