module.exports = (function () {

    const EXMO_URL = "https://exmo.com/en/";
    const EXMO_URL_TRADE = EXMO_URL + "trade#?pair={SHORTSYMBOL}_BTC";
    const REST_PUBLIC_EXMO = "https://api.exmo.com/v1/";
    const REST_TICKER_EXMO = REST_PUBLIC_EXMO + 'ticker/';


    return {
        REST_TICKER_EXMO: REST_TICKER_EXMO,
        EXMO_URL_TRADE: EXMO_URL_TRADE
    }

})();


