module.exports = (function () {

    const LIVECOIN_URL = "https://www.livecoin.net/";
    const LIVECOIN_URL_TRADE = LIVECOIN_URL + "en/trade/orderbook";
    const REST_PUBLIC_LIVECOIN = "https://api.livecoin.net/";
    const REST_CURRENCY_LIVECOIN  = REST_PUBLIC_LIVECOIN + "info/coinInfo";
    const REST_TICKER_LIVECOIN = REST_PUBLIC_LIVECOIN + "exchange/ticker";


    return {
        REST_CURRENCY_LIVECOIN: REST_CURRENCY_LIVECOIN,
        REST_TICKER_LIVECOIN: REST_TICKER_LIVECOIN,
        LIVECOIN_URL_TRADE: LIVECOIN_URL_TRADE

    }

})();


