const url = require('./url');
const array = require('./array');
const getNames = require('./format/getNames');
const requestModule = require('../../../requestModule');

module.exports = (function () {

    const CoinExchange = {

        key: 'CoinExchange',
        name: 'CoinExchange',
        getNamesForList: {
            needReq : true,
            req : {
                multi: false,
                fun: requestModule.fetchRequestForGettingTheNamesTroughProxy,
                url : {
                    url1: url.REST_CURRENCY_COINEXCHANGE,
                }
            },
            newNames : getNames.getNames
        },
        array: {
            array: array.COINEXCHANGE,
        },
        url: {
            publicURL : url.COINEXCHANGE_URL_TRADE,
            isCapital : true
        }

    };

    return {
        CoinExchange: CoinExchange
    }

})();


