module.exports = (function () {

    const COINEGG_URL = "https://www.coinegg.com/";
    const COINEGG_URL_TRADE = COINEGG_URL + "btc/{SHORTSYMBOL}/";
    const REST_PUBLIC_COINEGG = "https://api.coinegg.com/api/v1/";
    const REST_TICKER_COINEGG = REST_PUBLIC_COINEGG + 'allticker/region/btc';


    return {
        REST_TICKER_COINEGG: REST_TICKER_COINEGG,
        COINEGG_URL_TRADE: COINEGG_URL_TRADE
    }

})();


