const getNewCoinsFromExchange = require('../updateCoinList/getNewCoinsFromExchange');
const generateRefreshHTML = require('../GUI/refresh/generateRefreshHTML');

module.exports = (function () {

    function theNewCoins() {
        $('#all-new-coins').html(generateRefreshHTML.generateRefreshHTML());
        getNewCoinsFromExchange.allNewCoins(true);
    }

    return {
        theNewCoins: theNewCoins
    }

})();



