module.exports = (function () {


    const BITFINEX_URL = "https://www.bitfinex.com/";
    const BITIFINEX_URL_TRADE = BITFINEX_URL + "t/{SHORTSYMBOL}:BTC";
    const REST_PUBLIC_BITFINEX = "https://api.bitfinex.com/v1/";
    const REST_CURRENCY_BITFINEX = REST_PUBLIC_BITFINEX + "symbols";


    return {
        REST_CURRENCY_BITFINEX: REST_CURRENCY_BITFINEX,
        BITIFINEX_URL_TRADE: BITIFINEX_URL_TRADE
    }

})();


