module.exports = (function () {

    const POLONIEX_URL = "https://poloniex.com/";
    const POLONIEX_URL_TRADE = POLONIEX_URL + "exchange#btc_{SHORTSYMBOL}";
    const REST_PUBLIC_POLONIEX = "https://poloniex.com/public?command=";
    const REST_CURRENCY_POLONIEX = REST_PUBLIC_POLONIEX + "returnCurrencies";
    const REST_TICKER_POLONIEX = REST_PUBLIC_POLONIEX + "returnTicker";

    return {
        REST_CURRENCY_POLONIEX: REST_CURRENCY_POLONIEX,
        REST_TICKER_POLONIEX: REST_TICKER_POLONIEX,
        POLONIEX_URL_TRADE: POLONIEX_URL_TRADE
    }

})();


