const url = require('./url');
const array = require('./array');
const getNames = require('./format/getNames');
const requestModule = require('../../../requestModule');

module.exports = (function () {

    const Liqui = {

        key: 'Liqui',
        name: 'Liqui',
        getNamesForList: {
            needReq : true,
            req : {
                multi: false,
                fun: requestModule.fetchRequestForGettingTheNames,
                url : {
                    url1: url.REST_CURRENCY_LIQUI,
                }
            },
            newNames : getNames.getNames
        },
        array: {
            array: array.LIQUI,
        },
        url: {
            publicURL : url.LIQUI_URL_TRADE,
            isCapital : true
        }

    };

    return {
        Liqui: Liqui
    }

})();


