const url = require('./url');
const array = require('./array');
const getNames = require('./format/getNames');
const requestModule = require('../../../requestModule');

module.exports = (function () {

    const Poloniex = {

        key: 'Poloniex',
        name: 'Poloniex',
        getNamesForList: {
            needReq : true,
            req : {
                multi: true,
                fun: requestModule.multipleRequests,
                url : {
                    url1: url.REST_CURRENCY_POLONIEX,
                    url2: url.REST_TICKER_POLONIEX,
                }
            },
            newNames : getNames.getNames
        },
        array: {
            array: array.POLONIEX,
        },
        url: {
            publicURL : url.POLONIEX_URL_TRADE,
            isCapital : true
        }
    };


    return {
        Poloniex: Poloniex
    }

})();


