const url = require('./url');
const array = require('./array');
const getNames = require('./format/getNames');
const requestModule = require('../../../requestModule');

module.exports = (function () {

    const EXX = {

        key: 'EXX',
        name: 'EXX',
        getNamesForList: {
            needReq : true,
            req : {
                multi: false,
                fun: requestModule.fetchRequestForGettingTheNamesTroughProxy,
                url : {
                    url1: url.REST_TICKER_EXX,
                }
            },
            newNames : getNames.getNames
        },
        array: {
            array: array.EXX,
        },
        url: {
            publicURL : url.EXX_URL_TRADE,
            isCapital : false
        }
    };

    return {
        EXX: EXX
    }

})();


