module.exports = (function () {

    const COINBENE_URL = "https://www.coinbene.com/";
    const COINBENE_URL_TRADE = COINBENE_URL + "#/market?pairId=2";
    const REST_PUBLIC_COINBENE = "https://api.coinbene.com/v1/market/";
    const REST_TICKER_COINBENE = REST_PUBLIC_COINBENE + 'ticker?symbol=all';


    return {
        REST_TICKER_COINBENE: REST_TICKER_COINBENE,
        COINBENE_URL_TRADE: COINBENE_URL_TRADE
    }

})();


