module.exports = (function () {

    const HITBTC_URL = "https://hitbtc.com/";
    const HITBTC_URL_TRADE = HITBTC_URL + "exchange/{SHORTSYMBOL}-to-BTC";
    const REST_PUBLIC_HITBTC = "http://api.hitbtc.com/api/1/public/";
    const REST_CURRENCY_HITBTC = REST_PUBLIC_HITBTC + "symbols";
    const REST_TICKER_HITBTC = REST_PUBLIC_HITBTC + 'ticker';


    return {
        REST_TICKER_HITBTC: REST_TICKER_HITBTC,
        HITBTC_URL_TRADE: HITBTC_URL_TRADE
    }

})();


