module.exports = (function () {

    const LIQUI_URL = "https://www.binance.com/";
    const LIQUI_URL_TRADE = LIQUI_URL + "trade.html?symbol={SHORTSYMBOL}_BTC";
    const REST_PUBLIC_LIQUI = "https://api.liqui.io/api/3/";
    const REST_CURRENCY_LIQUI = REST_PUBLIC_LIQUI + 'info';


    return {
        REST_CURRENCY_LIQUI: REST_CURRENCY_LIQUI,
        LIQUI_URL_TRADE: LIQUI_URL_TRADE
    }

})();


