const url = require('./url');
const array = require('./array');
const getNames = require('./format/getNames');
const requestModule = require('../../../requestModule');

module.exports = (function () {

    const Bitrrex = {

        key: 'Bittrex',
        name: 'Bittrex',
        getNamesForList: {
            needReq : true,
            req : {
                multi: true,
                fun: requestModule.multipleRequestsWithProxy,
                url : {
                    url1: url.REST_CURRENCY_BITTREX,
                    url2: url.REST_TICKER_BITTREX,
                }
            },
            newNames : getNames.getNames
        },
        array: {
            array: array.BITTREX,
        },
        url: {
            publicURL : url.BITTREX_URL_TRADE,
            isCapital : true
        }
    };

    return {
        Bitrrex: Bitrrex
    }

})();


