const timeSelectorOnGraph = require('./timeSelectorOnGraph');
const requestStandard = require('../requestStandard');
const chartVars = require('../loadVars/chartVars');

module.exports = (function () {

    async function getDataToUpdateChart() {
        const timeStamp = $(this).attr('data-val');
        let url;
        timeSelectorOnGraph.makeTheRightOneActiveInTheModal(timeStamp);
        const requestData = requestStandard.requestFormat.find((el => el.key === '24'));
        url = requestData.url
            .replace('{SYM}', chartVars.vars.string.shortNameBigStockChart)
            .replace('{TSYM}', chartVars.vars.string.currencyBigStockChart)
            .replace('{time}', timeStamp);
        const chartData = await requestData.request(url);
        updateStockChart(chartData.Data)
    }

    const updateStockChart = function (chartData) {
        chartVars.vars.string.StockChart.dataProvider = [];
        $.each(chartData, function (index, item) {
            const time = moment.unix(item.time);
            const newtime = moment(time).format('YYYY-MM-DD HH:mm');
            chartVars.vars.string.StockChart.dataProvider.push({
                date: newtime,
                open: parseFloat((item.open).toFixed(8)),
                high: parseFloat((item.high).toFixed(8)),
                low: parseFloat((item.low).toFixed(8)),
                close: parseFloat((item.close).toFixed(8))
            });
        });
        chartVars.vars.string.StockChart.validateData();
    };




    return {
        getDataToUpdateChart: getDataToUpdateChart
    }

})();


