module.exports = (function () {

    const BINANCE_URL = "https://www.binance.com/";
    const BINANCE_URL_TRADE = BINANCE_URL + "trade.html?symbol={SHORTSYMBOL}_BTC";
    const REST_PUBLIC_BINANCE = "https://api.binance.com/api/v1/";
    const REST_TICKER_BINANCE = REST_PUBLIC_BINANCE + "ticker/24hr";


    return{
        REST_TICKER_BINANCE: REST_TICKER_BINANCE,
        BINANCE_URL_TRADE: BINANCE_URL_TRADE
    }

})();


