const newCoinVars = require('../loadVars/newCoinVars');
const allCoinData = require('../getArray/data/allCoinData');


module.exports = (function () {

    function getCoinsThatArentInTheList(coinData) {
        const firstExchangeArray = coinData.exchangeArray;
        const secondExchangeArray =  coinData.justGenerated[0];
        for (let i = 0; i < firstExchangeArray.length; i++){
            for (let f = 0; f < secondExchangeArray.length; f++){
                if (firstExchangeArray[i][2] === secondExchangeArray[f][2]){
                    secondExchangeArray.splice(f,1);
                }
            }
        }
        getComparedArray(secondExchangeArray, coinData.exchangeName);
    }

    function getComparedArray(firstExchangeArray, exchangeName) {
        const secondExchangeArray = allCoinData.allCoins;

        for (let i = 0; i < firstExchangeArray.length; i++){
            for (let f = 0; f < secondExchangeArray.length; f++){
                if (firstExchangeArray[i][2] === secondExchangeArray[f].shortName){
                    firstExchangeArray[i][0] = secondExchangeArray[f].longName;
                }
            }
        }
        if (firstExchangeArray.length != 0){
            newCoinVars.newCoinArray.push([exchangeName, firstExchangeArray]);
        }
    }

    return {
        getCoinsThatArentInTheList: getCoinsThatArentInTheList
    }

})();



