module.exports = (function () {

    const CCEX_URL = "https://c-cex.com/";
    const CCEX_URL_TRADE = CCEX_URL + "?p={SHORTSYMBOL}-btc";
    const REST_PUBLIC_CCEX = "https://c-cex.com/t/";
    const REST_CURRENCY_CCEX = REST_PUBLIC_CCEX + "coinnames.json";
    const REST_TICKER_CCEX = REST_PUBLIC_CCEX + "prices.json";


    return {
        REST_CURRENCY_CCEX: REST_CURRENCY_CCEX,
        REST_TICKER_CCEX: REST_TICKER_CCEX,
        CCEX_URL_TRADE: CCEX_URL_TRADE
    }

})();


