const loadAllCurrentCoins = require('./util/allCoins/loadAllCurrentCoins');
const loadAllNewCoins = require('./util/newCoins/generateHTMLForNewCoins');
const doSearch = require('./util/Search/doSearch');
const changeToNewOrAllCoins = require('./util/GUI/changeToNewOrAllCoins/changeToNewOrAllCoins');
const add = require('./util/loadMore/add.js');
const generate = require('./util/generateCoinInfo/generate');
const refresh = require('./util/refreshTheNewCoinData/refresh');
const atTopLevel = require('./util/newCoins/control/atTopLevel');
const showModal = require('./util/GUI/showModal/show');
const updateGraphForLocalStorage = require('../graph/updateGraphForLocalStorage');
// const createOneObjectWithAllCoins = require('./util/updateCoinList/createOneObjectWithAllCoins');

module.exports = (function () {

    const $container = $('.container');

    function showTheForm() {
        let $selShortName = $('#coin-info #the-sell-form .media-body .media-heading')[0].innerHTML;
        let $selLongName = $('#coin-info #the-sell-form .media-body p')[0].innerHTML;
        $('#coinName').val("shortName " + $selShortName + " longName " + $selLongName);
        $('.list-exchanges').hide();
        $('#gform').show();
    }

    function goBackToView() {
        $('.list-exchanges').show();
        $('#thankyou_message').hide();
    }
    
    function isOffline() {
        const $alert = $('.alert');
        const $sync = $('.refresh .btn');
        if (window.navigator.onLine) {
            $alert.hide();
            $sync.prop('disabled', false).removeClass('disabled');
        } else {
            $alert.show();
            $sync.prop('disabled', true).addClass('disabled');
        }
    }

    let init = function () {
        $('.container #form-exchange').on('change keyup paste click', '#searchBar', doSearch.forACertainCoin);
        $('.container #header-all-new').on('click', '.navbar-brand', changeToNewOrAllCoins.andBack);
        $container.on('click', '.generateMoreHtml', add.moreCoins);
        $container.on('click', '.row', generate.theInfoForTheCertainCoin);
        $container.on('click', '.update-info', showTheForm);
        $('.container .get-back').on('click', goBackToView);
        $('.container #all-new-coins').on('click', '.refresh .btn', refresh.theNewCoins);
        $container.on('click', '#get-chart', showModal.showModal);
        $('#show-more-graph .pagination').on('click', 'a', updateGraphForLocalStorage.getDataToUpdateChart);
        loadAllNewCoins.loadNewCoins();
        loadAllCurrentCoins.loadAllCoins();
        atTopLevel.controleAtTopLevel();
        window.addEventListener('offline', isOffline);
        window.addEventListener('online', isOffline);
        // createOneObjectWithAllCoins.createOneBigObject();

    };

    return {
        init: init
    }

})();



