const certainAmoutOfCoins = require('../generateTheCoinHTML/certainAmountOfCoins');
const allCoinData = require('../../../getArray/data/allCoinData');
const globalVars = require('../../../loadVars/globalVars');

module.exports = (function () {

    const dateTime = "24 May 2018";

    function loadAllCoins() {
        const value = certainAmoutOfCoins.toGenerate(allCoinData.allCoins, globalVars.var.counterAllCoins, 'a', 25);
        globalVars.var.counterAllCoins = value.counter;
        $("#all-coins").html(`
        <h1 class="info-many-coins">Current ${allCoinData.allCoins.length} coins available
            <small class="timestamp">Last time refreshed ${dateTime}}</small>
        </h1>
       ${value.$resultaatString}
       <button class="btn btn-coinchecker load-more-all generateMoreHtml">Load more<i class="fas fa-fw fa-spinner"></i></button>`);
    }



    return {
        loadAllCoins: loadAllCoins
    }

})();



