const allCoinData = require('../../../getArray/data/allCoinData');
const loadTheNewCoins = require('../newCoins/loadTheNewCoins');
const GUIModule = require('../../../GUIModule');

module.exports = (function () {

    function forACertainCoin() {
        let searchData = $('#searchBar').val().toLowerCase();
        let foundData;
        const $searchItems = $('#search-items');
        let ifNew;
        let coinData;
        $searchItems.hide();
        if (searchData === " " || searchData === "") {
            if ($('#All').hasClass('active')) {
                $('#all-coins').show();
            } else {
                $('#all-new-coins').show();
            }
        }
        else {
            $('#coin-info').hide();
            $searchItems.show();
            if ($('#All').hasClass('active')) {
                $('#all-coins').hide();
                coinData = allCoinData.allCoins;
                ifNew = 'a';
            } else {
                coinData = loadTheNewCoins.getTheNewCoins();
                $('#all-new-coins').hide();
                ifNew = 'n';
            }
            foundData = searchForMatchingNames(searchData, coinData);
            const newCompleteData = makeListOfNewSearchedCoins(foundData, coinData);
            let value = newCompleteData.map(function (el) {
                return GUIModule.generateTheHTMLOfFullAndShortName(el.element, el.indexValue, ifNew)
            });
            $searchItems.html(generateTheHTML(newCompleteData, value));
        }
    }

    function searchForMatchingNames(searchData, coinData) {
        return coinData.map(function (el) {
            return (el.longName.toLowerCase().indexOf(searchData) >= 0
                || el.shortName.toLowerCase().indexOf(searchData) >= 0)
        })
    }

    function makeListOfNewSearchedCoins(searchData, coinData) {
        let newObject = [];
        searchData.map(function (el, index) {
            if (el === true) {
                newObject.push({
                    indexValue: index,
                    element: coinData[index]
                })
            }
        });
        return newObject;
    }


    function generateTheHTML(foundData, value) {
        return `<div class="newInfo">
                       <h1 class="info-many-coins">Current ${foundData.length} coins</h1>
                       <div class="refresh">
                           <button type="button" class="btn btn-coinchecker"><i class="fas fa-fw fa-sync"></i></button>
                       </div>
                   </div>
                   ${value}`;
    }

    return {
        forACertainCoin: forACertainCoin
    }

})
();



