const certainAmoutOfCoins = require('../generateTheCoinHTML/certainAmountOfCoins');
const globalVars = require('../../../loadVars/globalVars');
const controlIf = require('./control/controleIf');
const loadTheNewCoins = require('./loadTheNewCoins');
const generateRefreshHTML = require('../GUI/refresh/generateRefreshHTML');

module.exports = (function () {


    function loadNewCoins() {
        if (!controlIf.weHaveNewCoins()) {
            $('#all-new-coins').html(generateRefreshHTML.generateRefreshHTML());
        } else {
            let coinData = loadTheNewCoins.getTheNewCoins();
            const value = certainAmoutOfCoins.toGenerate(coinData, globalVars.var.counterNewCoins, 'n', 25);
            globalVars.var.counterNewCoins = value.counter;
            $("#all-new-coins").html(`
                   <div class="newInfo">
                       <h1 class="info-many-coins">Current ${coinData.length} new listing of coins
                            <small class="timestamp">Last time refreshed ${localStorage.getItem("timeNewCoins")}</small>
                       </h1>
                       <div class="refresh">
                           <button type="button" class="btn btn-coinchecker"><i class="fas fa-fw fa-sync"></i></button>
                       </div>
                   </div>
                   ${value.$resultaatString}
                   <button class="btn btn-coinchecker load-more-new generateMoreHtml">Load more<i class="fas fa-fw fa-spinner"></i></button>`);

        }
    }

    return {
        loadNewCoins: loadNewCoins,
    }

})();



