require("babel-polyfill");
const initModule = require('./initModule');
const hideIt = require('./hideItModule');

let module = (function () {

    let serviceWorker = function () {
        if ('serviceWorker' in navigator) {
            navigator.serviceWorker
                .register('./serviceWorker.js')
                .then(function() { console.log('Service Worker Registered'); });
        }
    };

    let init = function () {
        serviceWorker();
        hideIt.startToHideIt();
    };

    return{
        init: init
    }

})();


let init = function () {
    module.init();
    let url = window.location.href;
    let moduleOfPage =  initModule.htmlPages.find((el) => ('http://localhost:8080/index.html').indexOf(el.html) > 1);
    moduleOfPage.init.init();
};

$(document).ready(init());







