module.exports = (function () {

    const makeTheRightOneActiveInTheModal = function (timeGraph) {
        let $selector;
        let message = '';
        switch (timeGraph) {
            case '24':
                $selector = "24HM";
                break;
            case '48':
                $selector = "48HM";
                break;
            case '120':
                $selector = "120HM";
                break;
            case '168':
                $selector = "168HM";
                message = 'Loading..';
                break;
            case '336':
                $selector = "336HM";
                message = 'Loading..';
                break;
            case '744':
                $selector = "744HM";
                message = 'Loading..';
                break;
        }
        let findtheSelected = $('.modal-body .pagination').find('.active');
        findtheSelected.removeClass('active');
        $('#' + $selector).addClass('active');
    };

    return {
        makeTheRightOneActiveInTheModal: makeTheRightOneActiveInTheModal
    }

})();


