module.exports = (function () {

    const TUXEXCHANGE_URL = "https://tuxexchange.com/";
    const TUXEXHCANGE_URL_TRADE = TUXEXCHANGE_URL + "trade?coin={SHORTSYMBOL}&market=BTC";
    const REST_PUBLIC_TUXEXCHANGE = "https://tuxexchange.com/api";
    const REST_TICKER_TUXEXCHANGE = REST_PUBLIC_TUXEXCHANGE + '?method=getticker';


    return {
        REST_TICKER_TUXEXCHANGE: REST_TICKER_TUXEXCHANGE,
        TUXEXHCANGE_URL_TRADE: TUXEXHCANGE_URL_TRADE
    }

})();


