module.exports = (function () {

    const BITSTAMP_URL = "https://www.bitstamp.net/";
    const BITSTAMP_URL_TRADE = BITSTAMP_URL + "market/tradeview/";
    const REST_PUBLIC_BITSTAMP = 'https://www.bitstamp.net/api/v2/';
    const REST_CURRENCY_BITSTAMP = REST_PUBLIC_BITSTAMP + '/trading-pairs-info/';


    return {
        REST_CURRENCY_BITSTAMP: REST_CURRENCY_BITSTAMP,
        BITSTAMP_URL_TRADE: BITSTAMP_URL_TRADE
    }

})();


