module.exports = (function () {

    const EXX_URL = "https://www.exxvip.com/";
    const EXX_URL_TRADE = EXX_URL + "tradePro/{SHORTSYMBOL}_btc";
    const REST_PUBLIC_EXX = "https://api.exx.com/data/v1/";
    const REST_TICKER_EXX = REST_PUBLIC_EXX + 'tickers';


    return {
        REST_TICKER_EXX: REST_TICKER_EXX,
        EXX_URL_TRADE: EXX_URL_TRADE
    }

})();


