const url = require('./url');
const array = require('./array');
const getNames = require('./format/getNames');
const requestModule = require('../../../requestModule');

module.exports = (function () {

    const Huobi = {

        key: 'Huobi',
        name: 'Huobi',
        getNamesForList: {
            needReq : true,
            req : {
                multi: false,
                fun: requestModule.fetchRequestForGettingTheNames,
                url : {
                    url1: url.REST_CURRENCY_HUOBI,
                }
            },
            newNames : getNames.getNames
        },
        array: {
            array: array.HUOBI,
        },
        url: {
            publicURL : url.HUOBI_URL_TRADE,
            isCapital : false
        }
    };

    return {
        Huobi: Huobi
    }

})();


