const exchangeModule = require('../../../exchanges/exchangeData');
const newCoinVars = require('../../../loadVars/newCoinVars');
const generateHTMLForNewCoins = require('../newCoins/generateHTMLForNewCoins');
const orderTheCoin = require('../order/orderTheCoins');

module.exports = (function () {

    async function allNewCoins(continueOrNot) {
        let data = [];
        let controleData;
        for (let item of exchangeModule.exchangeForNames){
            item = item.getNamesForList;
            if (item.needReq === true){
                if (item.req.multi === true) {
                    controleData = await item.req.fun([item.req.url.url1, item.req.url.url2]);
                } else {
                    controleData = await item.req.fun(item.req.url.url1);
                }
                if (controleData === null || controleData[0] === null || controleData[1] === null) {
                    if (item.req.multi === true) {
                        controleData = await item.req.fun([item.req.url.url1, item.req.url.url2]);
                    } else {
                        controleData = await item.req.fun(item.req.url.url1);
                    }
                } else {
                    await item.newNames(controleData);
                }
            } else {
                await item.newNames();
            }
        }
        //console.log(newCoinVars.newCoinArray);
        createABigObjectOfit(newCoinVars.newCoinArray,continueOrNot);
    }
    
    
    function createABigObjectOfit(newCoins,continueOrNot) {
        let element = Object.keys(newCoins).map(function (el) {
            return Object.keys(newCoins[el][1]).map(function (element) {
                let longName = '';
                if (!(newCoins[el][1][element][0] === 'IdontKnow')){
                    longName = newCoins[el][1][element][0]
                }
                return {
                    shortName: newCoins[el][1][element][2],
                    longName: longName,
                    exchanges: {
                        list: [newCoins[el][0]]
                    }
                }
            })

        });
        let newDataArrayOfNewCoins = [];
        for (let i = 0; i < element.length; i++){
            for (let f = 0; f < element[i].length; f++) {
                newDataArrayOfNewCoins.push(element[i][f])
            }
        }
        newDataArrayOfNewCoins = orderTheCoin.orderAlphaForShortName(newDataArrayOfNewCoins);
        localStorage.setItem("newCoins", JSON.stringify(newDataArrayOfNewCoins));
        localStorage.setItem("timeNewCoins", JSON.stringify((new Date()).toString().split(' ').splice(1,3).join(' ')));
        if (continueOrNot){
            generateHTMLForNewCoins.loadNewCoins();
        }
    }


    return {
        allNewCoins : allNewCoins
    }

})();



