const getImageNames = require('../../../../util/ImageNamesFix/shortNameImage');
const GUIModule = require('../../../../GUIModule');
const exchangeData = require('../../../../exchanges/exchangeData');
const request = require('../../../../requestStandard');
const graph = require('../../../../graph/createGraph');

module.exports = (function () {

    async function forOneCoin(data) {
        const coinData = data.coinData;
        const allOrNew = data.allOrNew;
        $('#the-sell-form').html(await createTopHTMLForSellForm(coinData));
        let $resultaatString = `
            <h1>Available on:</h1>
            <div>`;
        const htmlData = generateHTML(getUrls(coinData));
        Object.keys(htmlData).forEach(el => $resultaatString += htmlData[el]);
        if (window.navigator.onLine){
            $resultaatString += "</div><button class='btn btn-coinchecker update-info'>Update info</button>";
        } else {
            $resultaatString += "</div><button class='btn btn-coinchecker update-info disabled'>Update info</button>";
        }
        if (!allOrNew) {
            $resultaatString += "<p>Some info is missing!</p>"
        }
        $('.list-exchanges').html($resultaatString);
    }

    async function createTopHTMLForSellForm(coinData) {
        const longName = coinData.longName;
        const shortName = coinData.shortName;
        let $resultaatString = `
            <div id="the-sell-form">
                <div class="media-left">
                    ${GUIModule.controleImageName(longName, getImageNames.coinformatNames.find((el => shortName === el.shortName)), shortName)}
                </div>
                <div class="media-body">
                    <h3 class="media-heading">${longName}</h3>
                    <p>${shortName}</p>
                </div>`;
        let the24Change;
        let hist;
        if (window.navigator.onLine) {
            const requestFormat = request.requestFormat.find((el => el.key === '24'));
            the24Change = await requestFormat.request(requestFormat.url.replace('{SYM}', shortName).replace('{TSYM}', 'BTC').replace('{time}', 24));
            hist = the24Change.Data;
        } else {
            the24Change = {
                Type: 92562
            }
        }
        if (the24Change.Type === 100) {
            graph.create({
                dataGraph: hist,
                shortName: coinData.shortName,
                longName: coinData.longName,
                currency: "BTC"
            });
            return $resultaatString += `
                <div class="media-right">
                   <button class="btn btn-coinchecker" id="get-chart">
                        <i class="fas fa-fw fa-chart-line"></i>
                   </button>
                </div>
            </div>
            `
        } else {
            if (the24Change.Type === 92562) {
                return $resultaatString += `
            <div class="media-right">
                   <button class="btn btn-coinchecker disabled" id="get-chart">
                        <i class="fas fa-fw fa-chart-line"></i>
                   </button>
                </div>
            </div>`
            } else {
                return $resultaatString;
            }

        }


    }

    function getUrls(coinData) {
        return Object.keys(coinData.exchanges.list).map(function (key) {
            let data = (exchangeData.exchangeForNames.find(el => el.key === coinData.exchanges.list[key]).url);
            data.exchange = coinData.exchanges.list[key];
            data.shortName = coinData.shortName;
            return data;
        });
    }

    function generateHTML(urlData) {
        return urlData.map(function (el) {
            if (!el.isCapital) {
                el.shortName = (el.shortName).toString().toLowerCase();
            }
            el.publicURL = el.publicURL.replace('{SHORTSYMBOL}', el.shortName);
            return createOneElement(el)
        })
    }

    function createOneElement(el) {
        return `
            <div class="exchange-element">
                <p>${el.exchange}</p>
                <a class="btn btn-coinchecker" role="button" href="${el.publicURL}" target="_blank">Buy</a>
            </div>
            
        `
    }

    return {
        forOneCoin: forOneCoin
    }

})();



