const getImageNames = require('./util/ImageNamesFix/shortNameImage');

module.exports = (function () {

    function generateTheHTMLOfFullAndShortName(object, counter, newOrAllCoins) {
        const fullName = object.longName;
        const shortName = object.shortName;
        let $resultaatString = '';
        return `
        <div class="row" id="${newOrAllCoins}${counter}">
            <div class="col-xs-6">
            ${controleImageName(fullName, getImageNames.coinformatNames.find((el => shortName === el.shortName)), shortName)}
                <div class="media-body">
                    <h3 class='media-heading'>${fullName}</h3>
                    <p> [${shortName}]</p>
                </div>
                <div class='media-right'>
                    <h4 class='media-heading red'></h4>
                    <button class='btn btn-coinchecker' data-val='${fullName}' data-extra='${shortName}'>More info</button>
                </div>
            </div>
        </div>
`
    }

    function controleImageName(oldName, fullName, shortName) {
        switch (fullName) {
            case undefined:
                return `<div class="media-left"><img class='media-object' src='assets/media/${oldName}.jpg' alt="${oldName}" title="${shortName}"></div>`;
            default:
                let name;
                if (fullName.shortName === "BTM") {
                    name = oldName;
                } else {
                    name = fullName.photoName
                }
                return `<div class="media-left"><img class='media-object' src='assets/media/${name}.jpg' alt="${name}" title="${name}"></div>`;
        }
    }


    return {
        generateTheHTMLOfFullAndShortName: generateTheHTMLOfFullAndShortName,
        controleImageName: controleImageName
    }

})();



