module.exports = (function () {

    const BITZ_URL = "https://www.bit-z.com/";
    const BITZ_URL_TRADE = BITZ_URL + "exchange/{SHORTSYMBOL}_btc";

    const REST_PUBLIC_BITZ = "https://www.bit-z.com/api_v1/";
    const REST_TICKER_BITZ = REST_PUBLIC_BITZ + 'tickerall';


    return {
        REST_TICKER_BITZ: REST_TICKER_BITZ,
        BITZ_URL_TRADE: BITZ_URL_TRADE
    }

})();


