module.exports = (function () {

    const CRYPTOPIA_URL = "https://www.cryptopia.co.nz/";
    const CRYPTOPIA_URL_TRADE = CRYPTOPIA_URL + "Exchange/?market={SHORTSYMBOL}_BTC";
    const REST_PUBLIC_CRYPTOPIA = "https://www.cryptopia.co.nz/api/";
    const REST_CURRENCY_CRYPTOPIA = REST_PUBLIC_CRYPTOPIA + "GetCurrencies";
    const REST_TICKER_CRYPTOPIA = REST_PUBLIC_CRYPTOPIA + "GetMarkets/BTC";

    return {
        REST_CURRENCY_CRYPTOPIA: REST_CURRENCY_CRYPTOPIA,
        REST_TICKER_CRYPTOPIA: REST_TICKER_CRYPTOPIA,
        CRYPTOPIA_URL_TRADE: CRYPTOPIA_URL_TRADE
    }

})();


