module.exports = (function () {

    function andBack() {
        const $selector = $(this);
        const id$selector = $selector.attr('id');
        const $all = $('#All');
        const $new = $('#New');
        const $allCoins = $('#all-coins');
        const $allNewCoins = $('#all-new-coins');
        $('#gform, #coin-info, #thankyou_message').hide();
        switch (id$selector) {
            case 'All':
                $new.removeClass('active');
                $allNewCoins.hide();
                $allCoins.show();
                break;
            case 'New':
                $all.removeClass('active');
                $allCoins.hide();
                $allNewCoins.show();
                break;
        }
        $('#' + id$selector).addClass('active');
    }

    return {
        andBack: andBack
    }

})();



