const url = require('./url');
const array = require('./array');
const getNames = require('./format/getNames');
const requestModule = require('../../../requestModule');

module.exports = (function () {

    const Livecoin = {

        key: 'Livecoin',
        name: 'Livecoin',
        getNamesForList: {
            needReq : true,
            req : {
                multi: true,
                fun: requestModule.multipleRequestsWithProxy,
                url : {
                    url1: url.REST_CURRENCY_LIVECOIN,
                    url2: url.REST_TICKER_LIVECOIN,
                }
            },
            newNames : getNames.getNames
        },
        array: {
            array: array.LIVECOIN,
        },
        url: {
            publicURL : url.LIVECOIN_URL_TRADE,
            isCapital : true
        }
    };

    return {
        Livecoin: Livecoin
    }

})();


