module.exports = (function () {

    const COINEXCHANGE_URL = "https://www.coinexchange.io/";
    const COINEXCHANGE_URL_TRADE = COINEXCHANGE_URL + "market/{SHORTSYMBOL}/BTC";
    const REST_PUBLIC_COINEXCHANGE = "http://www.coinexchange.io/api/v1/";
    const REST_CURRENCY_COINEXCHANGE = REST_PUBLIC_COINEXCHANGE + "getmarkets";


    return {
        REST_CURRENCY_COINEXCHANGE: REST_CURRENCY_COINEXCHANGE,
        COINEXCHANGE_URL_TRADE: COINEXCHANGE_URL_TRADE
    }

})();


