const initModule = require('./initModule');

module.exports = (function () {

    function startToHideIt() {
        let url = window.location.href;
        let elements = initModule.htmlPages.find((el) => ('http://localhost:8080/index.html').indexOf(el.html) > 1);
        elements.elementsToHide.hide();
    }


    return{
        startToHideIt: startToHideIt,
    }

})();


