const allCoinData = require('../../../getArray/data/allCoinData');
const generateCoinInfo = require('../GUI/generateInfo/generateCoinInfo');
const loadTheNewCoins = require('../newCoins/loadTheNewCoins');
const generateRefreshHTML = require('../GUI/refresh/generateRefreshHTML');

module.exports = (function () {

    function theInfoForTheCertainCoin(e) {
        e.preventDefault();
        const $sel = $(this);
        let elNumber = $(this).attr('id');
        const $parent = $sel.parent();
        const allOrNew = elNumber.indexOf('a') >= 0;
        $parent.hide();
        $('#coin-info, .list-exchanges').show(
            generateRefreshHTML.generateRefreshHTML()
        );
        let data = '';
        if (allOrNew){
            elNumber = elNumber.replace('a', '');
            data = allCoinData.allCoins[elNumber];
        } else {
            elNumber = elNumber.replace('n', '');
            data = loadTheNewCoins.getTheNewCoins();
            data = data[elNumber];
        }
        generateCoinInfo.forOneCoin({
            coinData: data,
            allOrNew: allOrNew
        });
    }

    return {
        theInfoForTheCertainCoin: theInfoForTheCertainCoin
    }

})();



