const url = require('./url');
const array = require('./array');
const getNames = require('./format/getNames');
const requestModule = require('../../../requestModule');

module.exports = (function () {

    const Exmo = {

        key: 'Exmo',
        name: 'Exmo',
        getNamesForList: {
            needReq : true,
            req : {
                multi: false,
                fun: requestModule.fetchRequestForGettingTheNames,
                url : {
                    url1: url.REST_TICKER_EXMO,
                }
            },
            newNames : getNames.getNames
        },
        array: {
            array: array.EXMO,
        },
        url: {
            publicURL : url.EXMO_URL_TRADE,
            isCapital : true
        }
    };

    return {
        Exmo: Exmo
    }

})();


