module.exports = (function () {

    function generateRefreshHTML() {
        return (`
            <div class="wait">
               <i class="fas fa-fw fa-shipping-fast"></i>
               <h1>We are getting the data!</h1>
            </div>
        `);
    }

    return {
        generateRefreshHTML: generateRefreshHTML
    }

})();



