module.exports = (function () {

    const HUOBI_URL = "https://www.huobi.pro/";
    const HUOBI_URL_TRADE = HUOBI_URL + "{SHORTSYMBOL}_btc/exchange/";
    const REST_PUBLIC_HUOBI = "https://api.huobi.pro/";
    const REST_CURRENCY_HUOBI = REST_PUBLIC_HUOBI + "v1/common/symbols";


    return {
        REST_CURRENCY_HUOBI: REST_CURRENCY_HUOBI,
        HUOBI_URL_TRADE: HUOBI_URL_TRADE
    }

})();


