module.exports = (function () {

    let fetchRequestForGettingTheNames = function (exchangeUrl) {
        const url = exchangeUrl;
        let request = new Request(url, {
            method: 'GET',
            headers: new Headers()
        });
        return fetch(request)
            .then((resp) => resp.json())

    };

    let fetchRequestForGettingTheNamesTroughProxy = function (exchangeUrl) {
        const url = exchangeUrl;
        const yql_url = 'https://query.yahooapis.com/v1/public/yql?q=' + 'SELECT * FROM json WHERE url="' + encodeURIComponent(url) + '"&format=json';
        let request = new Request(yql_url, {
            method: 'POST',
            headers: new Headers()
        });
        return fetch(request)
            .then((resp) => resp.json())
            .then(function (data) {
                //console.log(data.query.results, url);
                if (data.query.results === null){
                    return data.query.results;
                } else {
                    if (exchangeUrl.indexOf('ex.trade') > 1 || exchangeUrl.indexOf('api.liqui.io') > 1) {
                        return (data.query.results);
                    } else {
                        return (data.query.results.json);
                    }
                }
            });
    };

    let multipleRequests = function (arrayOfRequests) {
        return Promise.all(arrayOfRequests.map(function (el) {
            return fetchRequestForGettingTheNames(el)
        }));
    };

    let multipleRequestsWithProxy = function (arrayOfRequests) {
        return Promise.all(arrayOfRequests.map(function (el) {
            if (el.indexOf('https://min-api.cryptocompare.com') >= 0)
                return fetchRequestForGettingTheNames(el);
            return fetchRequestForGettingTheNamesTroughProxy(el)
        }));
    };

    return {
        fetchRequestForGettingTheNames: fetchRequestForGettingTheNames,
        fetchRequestForGettingTheNamesTroughProxy: fetchRequestForGettingTheNamesTroughProxy,
        multipleRequests: multipleRequests,
        multipleRequestsWithProxy: multipleRequestsWithProxy
    }

})();


