const coinArrayOfKraken = require('../array');
const controlAllCoinDataWithOld = require('../../../../getArray/controlAllCoinDataWithOld');

module.exports = function () {

    function getNames() {
        const otherNewArray = [[]];
        controlAllCoinDataWithOld.getCoinsThatArentInTheList(
            {
                exchangeArray : coinArrayOfKraken.KRAKEN,
                justGenerated : otherNewArray,
                exchangeName: 'Kraken'

            }
        )
    }


    return {
        getNames: getNames
    }

}();


