const certainAmoutOfCoins = require('../generateTheCoinHTML/certainAmountOfCoins');
const allCoinData = require('../../../getArray/data/allCoinData');
const globalVars = require('../../../loadVars/globalVars');
const loadTheNewCoins = require('../newCoins/loadTheNewCoins');

module.exports = (function () {

    function moreCoins() {
        $(this).remove();
        if ($('#All').hasClass('active')) {
            moreAllCoins();
        } else {
            moreNewCoins();
        }
    }

    function moreNewCoins() {
        let countUpValue = 25;
        let coinData = loadTheNewCoins.getTheNewCoins();
        if (!(coinData.length > globalVars.var.counterNewCoins + 25)) {
            countUpValue = coinData.length - globalVars.var.counterNewCoins;
        }
        const value = certainAmoutOfCoins.toGenerate(coinData, globalVars.var.counterNewCoins, 'n', countUpValue);
        globalVars.var.counterNewCoins = value.counter;
        if (countUpValue === 25) {
            $("#all-new-coins").append(`
           ${value.$resultaatString}
           <button class="btn btn-coinchecker load-more-new generateMoreHtml">Load more<i class="fas fa-fw fa-spinner"></i></button>`
            );
        } else {
            $("#all-new-coins").append(`
           ${value.$resultaatString}`);
        }
    }

    function moreAllCoins() {
        let countUpValue = 25;
        if (!(allCoinData.allCoins.length > globalVars.var.counterAllCoins + 25)) {
            countUpValue = allCoinData.allCoins.length - globalVars.var.counterAllCoins;
        }
        const value = certainAmoutOfCoins.toGenerate(allCoinData.allCoins, globalVars.var.counterAllCoins, 'a', countUpValue);
        globalVars.var.counterAllCoins = value.counter;
        if (countUpValue === 25) {
            $("#all-coins").append(`
           ${value.$resultaatString}
           <button class="btn btn-coinchecker load-more-all generateMoreHtml">Load more<i class="fas fa-fw fa-spinner"></i></button>`
            );
        } else {
            $("#all-coins").append(`
           ${value.$resultaatString}`);
        }
    }

    return {
        moreCoins: moreCoins
    }

})();



