module.exports = (function () {

    const KUCOIN_URL = "https://www.kucoin.com/";
    const KUCOIN_URL_TRADE = KUCOIN_URL + "#/trade.pro/{SHORTSYMBOL}-BTC";
    const REST_PUBLIC_KUCOIN = "https://api.kucoin.com/v1/";
    const REST_TICKER_KUCOIN = REST_PUBLIC_KUCOIN + 'market/open/symbols';


    return {
        REST_TICKER_KUCOIN: REST_TICKER_KUCOIN,
        KUCOIN_URL_TRADE: KUCOIN_URL_TRADE
    }

})();


